@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400&display=swap);
.TodoCounter
{
	font-size: 24px;
	text-align: center;
	margin: 0;
	padding: 48px;
}

.TodoSearch
{
	display: block;
	width: 80%;
	height: 64px;
	margin: 0 auto;
	border-radius: 2px;
	border: 2px solid #202329;
	font-size: 24px;
	text-align: center;
	font-family: 'Montserrat';
	font-weight: 400;
	color: #1E1E1F;
	box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
	background: #F9FBFC;
}

.TodoSearch::-webkit-input-placeholder
{
	color: #A5A5A5;
	font-family: 'Montserrat';
	font-weight: 400;
}

.TodoSearch:-ms-input-placeholder
{
	color: #A5A5A5;
	font-family: 'Montserrat';
	font-weight: 400;
}

.TodoSearch::placeholder
{
	color: #A5A5A5;
	font-family: 'Montserrat';
	font-weight: 400;
}

.TodoSearch:focus
{
	outline-color: #61DAFA;
}

.TodoItem
{
	position: relative;
	display: flex;
	justify-content: center;
	align-items: center;
	width: 90%;
	margin: 2.4rem auto 0;
	box-shadow: 0 .5rem 5em rgba(32, 35, 41, 0.15);
	background-color: #FAFAFA;
}

.TodoItem-p
{
	margin: 2.4rem 0 2.4rem 2.4rem;
	width: calc(100% - 12rem);
	font-size: 1.8rem;
	line-height: 2.4re,;
	font-weight: 400;
}
.TodoItem-p--complete
{
	text-decoration: line-through;
}

.Icon
{
	cursor: pointer;
	display: flex;
	justify-content: center;
	align-items: center;
	height: 4.8rem;
	width: 4.8rem;
	font-size: 2.4rem;
	font-weight: bold;
}

.Icon-check
{
	position: absolute;
	left: 1.2rem;
}
.Icon-check--active
{
	color: #4caf50;
}

.Icon-delete
{
	position: absolute;
	top: -2.4re,;
	right: 0;
}
.Icon-delete:hover
{
	color: red;
}

.CreateTodoButton {
	background-color: #61DAFA;
	box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);
	border: none;
	border-radius: 50%;
	cursor: pointer;
	font-size: 50px;
	position: fixed;
	bottom: 24px;
	right: 24px;
	font-weight: bold;
	color: #FAFAFA;
	display: flex;
	justify-content: center;
	align-items: center;
	height: 64px;
	width: 64px;
	z-index: 1;
	-webkit-transform: rotate(0);
	        transform: rotate(0);
	transition: .3s ease;
  }

  .CreateTodoButton:hover {
	-webkit-transform: rotate(224deg);
	        transform: rotate(224deg);
  }

.ModalBackground {
	background: rgba(32,35,41,.8);
	position: fixed;
	top: -10px;
	left: -10px;
	right: -10px;
	bottom: -10px;
	display: flex;
	justify-content: center;
	align-items: center;
	color: white;
  }

form
{
	width: 90%;
	max-width: 300px;
	background-color: #fff;
	padding: 33px 40px;
	display: flex;
	align-content: center;
	flex-direction: column;
}

label
{
	text-align: center;
	font-weight: bold;
	font-size: 22px;
	margin-bottom: 26px;
	color: black;
}

textarea
{
	background-color: #F9FBFC;
	border: 2px, solid #202329;
	border-radius: 2px;
	box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
	color: #1E1E1F;
	font-size: 20px;
	text-align: center;
	padding: 12px;
	height: 96px;
	width: calc(100%-25px);
}

textarea::-webkit-input-placeholder
{
	color: #a5a5a5;
	font-family: 'Montserrat';
	font-weight: 400;
}

textarea:-ms-input-placeholder
{
	color: #a5a5a5;
	font-family: 'Montserrat';
	font-weight: 400;
}

textarea::placeholder
{
	color: #a5a5a5;
	font-family: 'Montserrat';
	font-weight: 400;
}

textarea:focus
{
	outline-color: #61DAFA;
}

.TodoForm-buttonContainer
{
	margin-top: 14px;
	display: flex;
	justify-content: space-between;
	align-items: center;
	width: 100%;
}

.TodoForm-button
{
	cursor: pointer;
	display: inline-block;
	font-size: 20px;
	color: #202329;
	font-weight: 400;
	width: 120px;
	height: 48px;
	border-radius: 2px;
	border: none;
	font-family: 'Montserrat';
}

.TodoForm-button-add
{
	background-color: #61DAFA;
	box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);
}

.TodoForm-button-cancel
{
	background: transparent;
}

*
{
	padding: 0;
	margin: 0;
	border: none;
	box-sizing: border-box;
	text-decoration: none;
	list-style: none;
}

html
{
	font-size: 62.5%;
	font-family: 'Open Sans', sans-serif;
}

body
{
	padding: 1rem;
}

a
{
	color: white;
	text-decoration: none;
}

a:hover
{
	text-decoration: underline;
}

