.TodoSearch
{
	display: block;
	width: 80%;
	height: 64px;
	margin: 0 auto;
	border-radius: 2px;
	border: 2px solid #202329;
	font-size: 24px;
	text-align: center;
	font-family: 'Montserrat';
	font-weight: 400;
	color: #1E1E1F;
	box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
	background: #F9FBFC;
}

.TodoSearch::placeholder
{
	color: #A5A5A5;
	font-family: 'Montserrat';
	font-weight: 400;
}

.TodoSearch:focus
{
	outline-color: #61DAFA;
}
